@import url("https://fonts.googleapis.com/css?family=Poppins:400,500,700&display=swap");

@import "normalize";
@import "variables";
@import "mixins";

* {
  box-sizing: border-box;
}

html {
  height: 100%;
  font-size: 10px;
}

body {
  min-height: 100%;
  @include font(1.6rem, #000000, 500, $text-ff, 1);
}

.main-title {
  display: none;
}

.header {
  background-color: $brand-color;
  padding: 15px 0;

  &__nav {
    display: flex;
    justify-content: space-between;
  }

  &__menu {
    display: flex;
    justify-content: space-between;
  }
}



.container {
  max-width: 1200px;
  width: 97%;
  margin: 0 auto;
}

.logo {
  font-size: 4.6rem;
  color: $title-color;
  text-decoration: none;
}

.menu {
  display: flex;
  padding: 0;


  &__link {
    text-decoration: none;
    color: $title-color;
  }

  &__item {
    list-style: none;
    padding: 5px;
    font-size: 2rem;
    text-transform: uppercase;
    margin-right: 50px;
    border-bottom: 3px solid transparent;

    @include hover {
      border-color: $title-color;
    }
  }

  &__socials {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__social {
    text-decoration: none;
    color: $title-color;
    font-size: 2.4rem;
    margin-right: 30px;

    @include hover {
      transform: scale(1.4);
    }
  }
}

.categories {
  background-color: #6A917F;
  margin: 0;

  &__list {
    padding: 15px 0;
    margin: 0;
  }

  &__link {
    text-decoration: none;
    color: #EFFFFA;

    @include hover {
      color: $title-color;
    }
  }

  &__item {
    list-style: none;
    display: inline;
    margin-right: 50px;
    font-size: 2rem;
  }
}

.section {
  padding: 50px 0;
}

.blocks {
  display: flex;
  justify-content: space-between;
  // gap: 50px;

  &--bottom {}

}

.m-right {
  margin-right: 50px;

}

.m-bottom {
  margin-bottom: 50px;

}

.w2 {
  width: 30%;
}

.w3 {
  // flex-basis: 65%;
  width: 65%;
}

.flex-col {
  flex-direction: column;

}

.block {


  &__content {
    background-color: $block-color;
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-content: space-between;

    &--lg {
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-between;

      &:nth-last-child(2n) {
        // margin-right: 35px;
      }

    }
  }

  &__title {
    text-transform: uppercase;
    color: $title-color;
    font-size: 2rem;
  }
}

.preview {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 35px;

  &--lg {
    width: 47%;

    .preview__text {
      flex-basis: 65%;
    }

    .preview__title {
      max-width: 340px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
    }

    &:nth-child(3) {
      margin: 0;
    }
  }

  &:last-child {
    margin: 0;
  }

  &__img-wrap {
    width: 150px;
    height: 150px;
    overflow: hidden;
    position: relative;
    // flex-basis: 35%;
  }

  &__img {
    display: block;
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

  }

  &__text {
    flex-basis: 75%;
    line-height: 1.3;
  }

  &__title {
    font-size: 1.8rem;
    margin: 0;
    max-width: 500px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &__cat {
    color: $subtext-color;
  }

  &__par {
    margin: 15px 0 0 0;
    max-height: 60px;
    overflow: hidden;
  }
}

.a-list {
  padding: 0;
  margin: 0;

  &__item {
    list-style: none;
    display: flex;
    margin-bottom: 15px;
  }

  &__time {
    margin-right: 10px;
    width: 40%;
  }

  &__title {
    font-weight: 700;
    max-height: 32px;
    max-width: 175px;
    overflow: hidden;
  }
}

.comment {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;

  &__img-wrap {
    width: 100px;
    height: 100px;
    overflow: hidden;
    position: relative;
  }

  &__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__text {
    word-wrap: break-word;
    width: 60%;
  }

  &__username {
    display: block;
    font-weight: 700;
    max-height: 16px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &__article {
    display: block;
    color: $subtext-color;
    max-height: 16px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &__par {
    margin-top: 10px;
    display: block;
    max-height: 64px;
    overflow: hidden;
  }
}

.form {
  display: flex;
  justify-content: space-between;

  &__input {
    width: 75%;
    padding: 8px 30px;
    border: none;

  }

}

.btn {
  padding: 8px 30px;
  text-transform: uppercase;
  background-color: $title-color;
  color: $block-color;
  border: none;
  cursor: pointer;

  @include hover {
    transform: scale(1.1);
  }
}


footer {
  background-color: #ACF8CD;

}

.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 0;

  &__logo {

    display: flex;
    flex-direction: column;
    // justify-content: space-between;
    // align-content: space-between;
  }

  &__copyright {
    margin-top: 50px;
    font-size: 1.4rem;
    color: $subtext-color;
  }


  .menu {
    flex-direction: column;
    align-items: center;

  }
}